import React, { useState, useEffect } from 'react';
import axios from 'axios';

import { Swiper, SwiperSlide, SwiperSlideProps } from 'swiper/react';
import { Scrollbar, Navigation } from 'swiper';
import SwiperCore from 'swiper';

import './saleSlider.scss';
import 'swiper/scss';
// import 'swiper/scss/pagination';
import 'swiper/css/scrollbar';
import SizeSet from '../ProductMenu/components/size';
import { useParams } from 'react-router-dom';

export const SaleSlider = ({ path }) => {
  const useSwiperRef = () => {
    const [wrapper, setWrapper] = useState(null);
    const ref = React.useRef(null);

    useEffect(() => {
      if (ref.current) {
        setWrapper(ref.current);
      }
    }, []);

    return [wrapper, ref];
  };

  const [arr, setArr] = useState([]);
  useEffect(() => {
    axios.get(`http://localhost:8080/products?sale=true`).then(({ data }) => setArr(data));
  }, []);

  const [nextEl, nextElRef] = useSwiperRef();
  const [prevEl, prevElRef] = useSwiperRef();

  return (
    <div className="swiper__sale">
      <Swiper
        spaceBetween={50}
        slidesPerView={3}
        // onBeforeInit={(swiper) => {
        //   swiperRef.current = swiper;
        // }}

        navigation={{
          prevEl,
          nextEl,
          disabledClass: 'hidden', // <-- css classes are added automatically
        }}
        scrollbar={{
          hide: false,
          draggable: true,
        }}
        modules={[Scrollbar, Navigation]}
        className="saleSlider">
        {arr.map((item) => (
          <SwiperSlide key={item.id} className="saleSlider__content">
            <div className="saleSlider__content-row">
              <div className="saleSlider__content-row--img">
                <img src={item.imageUrl} alt={item.title}></img>
                <div className="saleSlider__content-row--ico"></div>
              </div>
              <h1>{item.title}</h1>
              {/* {category === 'rolls' ||
              category === 'sets' ||
              category === 'shawarma' ||
              category === 'soups' ||
              category === 'salads' ||
              category === 'hotdogs' ||
              category === 'woks' ? (
                <p className="saleSlider__content-row--descrip">{item.ingredients.join(', ')}.</p>
              ) : category === 'pizza' ? (
                <SizeSet item={item} />
              ) : category === 'drinks' || category === 'fries' || category === 'combo' ? (
                <p className="product__menu--subtitle">{item.ingredients}.</p>
              ) : (
                ''
              )} */}
              <SizeSet item={item} />
              <p className="saleSlider__content-row--weight">{item.weight} гр.</p>
              <p className="saleSlider__content-row--price">{item.price} руб.</p>
              <button className="saleSlider__content-row--basket">
                <svg
                  width="54"
                  height="54"
                  viewBox="0 0 54 54"
                  fill="none"
                  xmlns="http://www.w3.org/2000/svg">
                  <circle cx="27" cy="27" r="27" fill="#FF7134" fillOpacity="0.53" />
                  <path
                    d="M27.5 27.4991C27.9204 27.4991 28.2617 27.1578 28.2617 26.7374C28.2617 26.317 27.9204 25.9757 27.5 25.9757C27.0795 25.9757 26.7383 26.317 26.7383 26.7374C26.7383 27.1578 27.0795 27.4991 27.5 27.4991Z"
                    fill="black"
                  />
                  <path
                    d="M43.9531 21.4053H39.4485L32.5193 11.8723C31.8968 10.8056 30.5257 10.4259 29.4367 11.029C28.3328 11.6423 27.9359 13.0327 28.5494 14.1369C28.567 14.1681 28.5863 14.1982 28.608 14.2267L34.043 21.4053H20.9569L26.3919 14.2267C26.4133 14.1982 26.433 14.1681 26.4505 14.1369C27.0626 13.0354 26.6701 11.6437 25.5623 11.0284C24.4757 10.4265 23.1046 10.8059 22.481 11.8717L15.5514 21.4053H11.0469C9.36693 21.4053 8 22.7723 8 24.4522C8 25.7772 8.85991 26.9308 10.0712 27.339C10.2004 27.439 10.3616 27.4991 10.5372 27.4991C10.5723 27.4991 10.6053 27.5012 10.6398 27.5059C10.9677 27.55 11.2275 27.7987 11.287 28.1242L13.8783 42.3809C14.0762 43.4678 15.0218 44.2569 16.1269 44.2569H38.8731C39.9781 44.2569 40.9237 43.4678 41.1216 42.3806L43.7129 28.1245C43.7724 27.7987 44.0325 27.55 44.3616 27.5059C44.3949 27.5012 44.4279 27.4991 44.4628 27.4991C44.6395 27.4991 44.802 27.4384 44.9311 27.3372C46.1433 26.9263 46.9999 25.7748 46.9999 24.4522C46.9999 22.7723 45.633 21.4053 43.9531 21.4053ZM29.8604 13.357C29.6863 12.9967 29.8214 12.5578 30.1755 12.3614C30.5442 12.157 31.0089 12.2888 31.2121 12.6551C31.227 12.6825 31.244 12.7087 31.2621 12.7337L37.5651 21.4053H35.9535L29.8604 13.357ZM23.7378 12.734C23.7559 12.709 23.7723 12.6831 23.7875 12.6563C23.9913 12.2897 24.4564 12.1576 24.8235 12.3608C25.1782 12.5578 25.3139 12.9961 25.1395 13.357L19.0464 21.4053H17.4349L23.7378 12.734ZM43.9531 25.9757H30.5468C30.1261 25.9757 29.7851 26.3166 29.7851 26.7374C29.7851 27.1581 30.1261 27.4914 30.5468 27.4914H42.3076C42.2678 27.605 42.2362 27.7229 42.2142 27.8443L39.6226 42.108C39.5568 42.4704 39.2414 42.7335 38.8731 42.7335H16.1269C15.7582 42.7335 15.4431 42.4704 15.3771 42.1083L12.7857 27.8517C12.7637 27.7303 12.7322 27.6125 12.6923 27.4991H24.4531C24.8738 27.4991 25.2148 27.1581 25.2148 26.7374C25.2148 26.3166 24.8738 25.9757 24.4531 25.9757H11.0469C10.1998 25.9757 9.52343 25.2854 9.52343 24.4522C9.52343 23.6123 10.2069 22.9288 11.0469 22.9288H43.9531C44.793 22.9288 45.4765 23.6123 45.4765 24.4522C45.4765 25.2981 44.7844 25.9757 43.9531 25.9757Z"
                    fill="black"
                  />
                  <path
                    d="M27.5 29.7843C26.2399 29.7843 25.2148 30.8093 25.2148 32.0694V38.1632C25.2148 39.4233 26.2399 40.4483 27.5 40.4483C28.7601 40.4483 29.7851 39.4233 29.7851 38.1632V32.0694C29.7851 30.8069 28.7625 29.7843 27.5 29.7843ZM28.2617 38.1632C28.2617 38.5833 27.9201 38.9249 27.5 38.9249C27.0798 38.9249 26.7382 38.5833 26.7382 38.1632V32.0694C26.7382 31.6493 27.0798 31.3077 27.5 31.3077C27.9204 31.3077 28.2617 31.649 28.2617 32.0694V38.1632Z"
                    fill="black"
                  />
                  <path
                    d="M35.1171 40.4483C36.3772 40.4483 37.4023 39.4233 37.4023 38.1632V32.0694C37.4023 30.8069 36.3796 29.7843 35.1171 29.7843C33.857 29.7843 32.832 30.8093 32.832 32.0694V38.1632C32.832 39.4233 33.857 40.4483 35.1171 40.4483ZM34.3554 32.0694C34.3554 31.6493 34.697 31.3077 35.1171 31.3077C35.5376 31.3077 35.8788 31.649 35.8788 32.0694V38.1632C35.8788 38.5833 35.5373 38.9249 35.1171 38.9249C34.697 38.9249 34.3554 38.5833 34.3554 38.1632V32.0694Z"
                    fill="black"
                  />
                  <path
                    d="M19.8828 29.7843C18.6227 29.7843 17.5977 30.8093 17.5977 32.0694V38.1632C17.5977 39.4233 18.6227 40.4483 19.8828 40.4483C21.1429 40.4483 22.168 39.4233 22.168 38.1632V32.0694C22.168 30.8069 21.1453 29.7843 19.8828 29.7843ZM20.6445 38.1632C20.6445 38.5833 20.3029 38.9249 19.8828 38.9249C19.4627 38.9249 19.1211 38.5833 19.1211 38.1632V32.0694C19.1211 31.6493 19.4627 31.3077 19.8828 31.3077C20.3032 31.3077 20.6445 31.6487 20.6445 32.0694V38.1632Z"
                    fill="black"
                  />
                </svg>
              </button>
            </div>
          </SwiperSlide>
        ))}
      </Swiper>
      {/* <button
        className={'swiper__sale-button-prev'}
        onClick={() => swiperRef.current?.slidePrev()}></button>
      <button
        className="swiper__sale-button-next"
        onClick={() => swiperRef.current?.slideNext()}></button> */}

      <button ref={prevElRef} className="swiper__sale-button-prev"></button>
      <button ref={nextElRef} className="swiper__sale-button-next"></button>
    </div>
  );
};
