import React, { FC, useState } from 'react';
import { Link, NavLink, useNavigate } from 'react-router-dom';
import { Swiper, SwiperSlide, SwiperSlideProps } from 'swiper/react';
import { Navigation } from 'swiper';

import './productSlider.scss';
import RollsIco from '../../assets/catalog/svg/rol.svg';
import PizzasIco from '../../assets/catalog/svg/pizza.svg';
import SoupsIco from '../../assets/catalog/svg/soup.svg';
import SaladsIco from '../../assets/catalog/svg/salad.svg';
import DesertsIco from '../../assets/catalog/svg/desert.svg';
import DrinksIco from '../../assets/catalog/svg/drink.svg';
import SetsRollsIco from '../../assets/catalog/svg/setsroll.svg';
import ShawarmaIco from '../../assets/catalog/svg/shawarma.svg';
import BurgerIco from '../../assets/catalog/svg/burger.svg';
import FriesIco from '../../assets/catalog/svg/fries.svg';
import ComboIco from '../../assets/catalog/svg/combo.svg';
import HotDogIco from '../../assets/catalog/svg/hotdog.svg';
import WokIco from '../../assets/catalog/svg/wok.svg';

const ProductSlider = () => {
  const Menu = [
    {
      id: '1',
      ico: RollsIco,
      title: 'Роллы',
      link: 'rolls',
      color: 'rollscol',
    },
    {
      id: '2',
      ico: SetsRollsIco,
      title: 'Сеты',
      link: 'sets',
      color: 'setscol',
    },
    {
      id: '3',
      ico: PizzasIco,
      title: 'Пицца',
      link: 'pizza',
      color: 'pizzacol',
    },
    {
      id: '4',
      ico: ShawarmaIco,
      title: 'Шаурма',
      link: 'shawarmas',
      color: 'shawarmacol',
    },
    {
      id: '5',
      ico: SoupsIco,
      title: 'Супы',
      link: 'soups',
      color: 'soupscol',
    },
    {
      id: '6',
      ico: SaladsIco,
      title: 'Салаты',
      link: 'salads',
      color: 'saladscol',
    },
    {
      id: '7',
      ico: DesertsIco,
      title: 'Десерты',
      link: 'deserts',
      color: 'desertscol',
    },
    {
      id: '8',
      ico: DrinksIco,
      title: 'Напитки',
      link: 'drinks',
      color: 'drinkscol',
    },

    {
      id: '9',
      ico: BurgerIco,
      title: 'Бургеры',
      link: 'burgers',
      color: 'burgerscol',
    },
    {
      id: '10',
      ico: FriesIco,
      title: 'Закуски',
      link: 'fries',
      color: 'friescol',
    },
    {
      id: '11',
      ico: ComboIco,
      title: 'Комбо',
      link: 'combos',
      color: 'combocol',
    },
    {
      id: '12',
      ico: HotDogIco,
      title: 'Хот-доги',
      link: 'hotdogs',
      color: 'hotdogscol',
    },
    {
      id: '13',
      ico: WokIco,
      title: 'Wok',
      link: 'woks',
      color: 'wokscol',
    },
  ];

  const useSwiperRef = () => {
    const [wrapper, setWrapper] = useState(null);
    const ref = React.useRef(null);

    React.useEffect(() => {
      if (ref.current) {
        setWrapper(ref.current);
      }
    }, []);

    return [wrapper, ref];
  };

  const [nextEl, nextElRef] = useSwiperRef();
  const [prevEl, prevElRef] = useSwiperRef();

  const [activeItem, setActiveItem] = React.useState(0);
  const navigate = useNavigate();

  return (
    <div className="swiper__product">
      <Swiper
        spaceBetween={56}
        slidesPerView={6}
        navigation={{
          prevEl,
          nextEl,
          disabledClass: 'hidden',
        }}
        modules={[Navigation]}
        className="productSlider">
        {Menu.map((Menu, idx) => (
          <SwiperSlide key={idx} className="productSlider__content ">
            {/* <div
              key={Menu.id}
              onClick={() => setActiveItem(Menu.id)}
              className={activeItem === Menu.id ? 'active' : ''}> */}
            <div
              className={`productSlider__content-row ${activeItem === idx ? 'active' : ''}`}
              onClick={() => navigate(`/${Menu.link}`, setActiveItem(idx))}>
              <div className={`productSlider__content-row--img ${Menu.color}`}>
                <img
                  className="productSlider__content-row--img-ico"
                  src={Menu.ico}
                  alt={Menu.title}></img>
              </div>
              <h1 className="productSlider__content-row--title">{Menu.title}</h1>
            </div>
          </SwiperSlide>
        ))}
      </Swiper>
      <button ref={prevElRef} className="swiper__product-button-prev"></button>
      <button ref={nextElRef} className="swiper__product-button-next"></button>
    </div>
  );
};

export default ProductSlider;
